@import 'config/vars';

.customers{
    display: flex;
    margin-top: 187px;
    &__btns{
        margin-top: 76px;
        margin-left: 4px;
    
    }
}
.customers__btns img:nth-child(2){
    margin-left: 10px;
}
.customers__right{
    margin-left: 89px;
    margin-top: -3px;
    letter-spacing: -0.92px;
    line-height: 73px;
}
.customers__header{
    h1{
        font-family: 'Jost';
        font-weight: 900;
        font-size: 64px;
    }
    
}
.estimation{
    display: flex;
    align-items: center;
    margin-top: 14px;
    margin-left: 5px;
    p{
        font-family: 'Jost';
        font-weight: 600;
       
        font-size: 40px;
    }
}
.stars{
    margin-top: 23px;
    margin-left: 27px;
    h6{
        font-family: 'Jost';
        font-weight: 400;
        font-size: 20px;
        color:$gray;
        margin-top: -15px;
        letter-spacing: 0.02px;
    }
}
.stars__items{
    display: flex;
    align-items: center;
}
.customers__items{
    display: flex;
    align-items:center ;
}
.swiper{
    width: 793px;
}
.swiper-wrapper{
   left: -211px; 
   position: relative;
 
}
.swiper-slide{
    width: 515px;
    
}
div.swiper-slide.swiper-slide-active{
    width: 515px !important;
    
}
div.swiper-slide.swiper-slide-next{
    width: 515px !important;
    
}
@media screen  and (max-width:1366px){
    .swiper{
        width: 660px;
    }
}
@media screen  and (max-width:1250px){
    
    .customers__header{
        h1{
            font-family: 'Jost';
            font-weight: 900;
            font-size: 56px;
        }
        
    }
}
@media screen  and (max-width:1180px){
    .swiper{
        width: 570px;

    }
    .swiper-wrapper{
        left: 25px;
    }
    .customers__header{
        h1{
            font-family: 'Jost';
            font-weight: 900;
           font-size: 53px;
        }
        
    }
    .estimation p {
        font-size: 36px;
    }

    .customers__btns{
        margin-top: 40px;
    }
    .personImg{
        width: 430px;
    }
    .customer__item{
        width: 430px;
    }
    .swiper-slide{
        width: 490px;
        
    }
    div.swiper-slide.swiper-slide-prev{
        width: 490px !important;
        
    }
    div.swiper-slide.swiper-slide-active{
        width: 490px !important;
        
    }
    div.swiper-slide.swiper-slide-next{
        width: 490px !important;
        
    }
   
}

@media screen and (max-width:1125px) {
    .customers__header h1{
        font-size: 46px;
        line-height: 60px;
    }
}

@media screen and (max-width:1070px) {
    .customers__right {
        margin-left: 45px;
    }
}