@import "config/mixins";
.menu{
    margin-left: 244px;
    @include centerAll;
}
.menu__wrapper{
    @include centerAll;
}
.menu_item{
    margin-right: 40px;
}
.menu_link{
    color: black;
    text-decoration: none;
    font-family: 'Jost';
    font-weight: 500;
    font-size: 15px;
}
@media screen and (max-width:1325px) {

    .menu{
       position: absolute;
       width: 400px;
       height: 100vh;
       top: 0;
       background: #b5e2b2;
       left: -450px;
       z-index: 1;
       justify-content: flex-start;
        margin-left: 0;
        flex-direction: column;
        align-items: flex-start;
        transition: all ease 1s;
        &__wrapper{
            padding-top: 54px;
            padding-left: 55px;
            flex-direction: column;
            align-items: flex-start;
        }
    &_item{
        margin-bottom: 40px;
        
       
    }
    &_link{
         font-size: 25px;
    }
    }
    
    
    
}


