@import 'config/mixins';
@import 'config/vars';

.order{
    margin-top: 190px;
    margin-left: -95px;
    display: flex;
    align-items: center;
    &__title{
        font-family: 'Jost';
        font-size: 64px;
        margin-left: 16px;
        letter-spacing: -1.1px;
        font-weight: 900;
        line-height: 70px;
    }
    &__left{
        margin-left: -6px;
    }
    &__right{
        margin-top: 13px;
        width: 560px;
        margin-left: 6px;
    }
    &__subtitle{
        font-family: 'Jost';
        font-size: 24px;
        font-weight: 400;
        color: $gray;
        margin-top: 30px;
        margin-bottom: 60px;
        margin-left: 16px;
    }
    &__btn{
        @include btnBlack;
        padding: 20px 40px;
        border-radius: 126px;
        font-size: 18px;
        font-weight: 500;
        margin-left: 16px;
        margin-top: 3px;
        
    }
}
.bghomecoffe{
    position: absolute;
    left: -117px;
    top: -141px;
}
.homeCoffe{
    position: relative;
    margin-left: 16px; 
    margin-top: 217px;
}
@media screen and (max-width:1325px) {
    .order{
        margin-top: 160px;
        justify-content: center;
        &__title{
            font-size: 50px;
            line-height: 56px;
        }
        &__subtitle{
            font-size: 22px;
        }
        &__right{
            width: 460px;
        }
    }
    .bghomecoffe{
        left: -50px;
        top: -190px;
    }
    .homeImg{
        left: 40px;
       width: 560px;
    }
    .orderImg{
        width: 660px;
    }
}
@media screen and (max-width:1280px) {
    .bghomecoffe{
        left: -140px;
    }
    .homeImg{
        width: 500px;
    }
    .homeCoffe{
        margin-top: 160px;
    }
    .order{
        &__right{
            width: 460px;
        }
    }
    .home__right{
        width: 460px;
        margin-top: 18px;
    }
}
@media screen and (max-width:1190px) {
    .bghomecoffe{
        left: -92px;
    }
}
@media screen and (max-width:1150px) {
    .homeImg{
        width: 450px;
    }
}
@media screen and (max-width:1120px) {
    .order{
        
        &__title{
            font-size: 50px;
            line-height: 56px;
        }
        &__subtitle{
            font-size: 22px;
        }
    }
    .orderImg{
        width: 580px;
    }
    .bghomecoffe{
        left: -110px;
    }
    .homeImg{
        width: 430px;
        left: 70px;
    }
    .home__right{
        width: 420px;
    }
}
@media screen and (max-width:1050px) {
    .bghomecoffe {
        left: -90px;
    }
    .homeCoffe {
        margin-top: 200px;
    }

}