@import 'config/mixins';
@import 'config/vars';

.main{
    @include justifyBeetwen;
    margin-top: 120px;
    &__left{
        max-width: 567px;
        position: relative;
    }
    &__title{
        font-family: 'Jost';
        font-weight: 900;
        font-size: 124px;
        line-height: 140px;
        letter-spacing: -1.7px;
        margin-bottom: 30px;
    }
    &__subtitle{
        font-family: 'Jost';
        font-weight: 400;
        font-size: 24px;
        color: $gray;
    }
    &__btns{
        display: flex;
        align-items: center;
        margin-top: 60px;
    }
    &__btnDownload{
        @include btnBlack;
        padding: 20px 40px;
        border-radius: 126px;
        font-size: 18px;
        font-weight: 500;
    }
    &__btnShop{
        font-family: 'Jost';
        color: black;
        font-size: 18px;
        font-weight: 500;
        border-bottom: 1px solid  black;
        text-transform: uppercase;
        margin-left: 30px;
    }
    &__right{
        margin-left: 10px;
        margin-top: 2px;
        img{
            position: relative;
            z-index: -1;
        }
    }

}
.cafeMain{
    position: absolute;
    left: 438px;
    top: 337px;
}

@media screen and (max-width:1325px) {
    .main{
        &__title{
            font-size: 100px;
        }
        &__subtitle{
            font-size: 23px;
        }

        &__right{
            img{
            width: 600px;
        }
    }
    }

    .cafeMain{
       
        left: 343px;
        top: 332px;
    }
}
@media screen and (max-width:1280px) {
    .main{
        &__title{
            font-size: 90px;
            line-height: 110px;
        }
        &__subtitle{
            font-size: 22px;
        }

        &__right{
            img{
            width: 550px;
        }
    }
    }

    .cafeMain{
       
        left: 321px;
        top: 259px;
    }
}
@media screen and (max-width:1180px) {
    .main{
        &__title{
            font-size: 90px;
            line-height: 110px;
        }
        &__subtitle{
            font-size: 22px;
        }

        &__right{
            img{
            width: 500px;
        }
    }
    }

    .cafeMain{
        left: 321px;
        top: 259px;
    }
}