@import 'config/vars';
@import 'config/mixins';


.footer{
    margin-top: 210px;
    &__wrapper{
        display: flex;
        justify-content: space-between;
        
    }
    &__right{
        margin-right: 90px;
    }
    &__subtitle{
        margin-top: 40px;
        font-family: 'Jost';
        font-size: 30px;
        font-weight: 400;
        color: $gray;
        max-width: 558px;
        line-height: 40px;
    }
    &__title{
        margin-top: 104px;
        font-family: 'Jost';
        font-size: 40px;
        font-weight: 900;
        letter-spacing: -0.6px;
    }
    &__subtitle-text{
        font-family: 'Jost';
        font-size: 30px;
        font-weight: 400;
        color: $gray;
        line-height: 40px;
        max-width: 558px;
        margin-top: 25px;
        margin-bottom: 34px;
    }
    &__btn-find{
        font-family: 'Jost';
        font-size: 18px;
        font-weight: 500;
        color: black;
        text-transform: uppercase;
        position: relative;
        margin-bottom: 30px;
        padding-bottom: 30px;
        letter-spacing: 0.3px;
    }
}

.footer__btn-find::after{
    content: '';
    display: inline-block;
    width: 30px;
    height: 30px;
    position: absolute;
    background: url('../../images//arrow-right\ 1.png');
    top: -2px;
    right: -34px;
}

.contact{
    
    &__items{
        margin-top: 30px;
        display: flex;
        align-items: center;
        margin-left: -47px;
        img{
            margin-left: 24px;
        }
    }
    &__title{
        margin-top: 17px;
        margin-left: -20px;
        letter-spacing: -0.4px;
        font-family: 'Jost';
        font-size: 40px;
        font-weight: 900;
    }
    &__title-two{
        margin-top: 85px;
        font-family: 'Jost';
        font-size: 40px;
        font-weight: 900;
        margin-left: -21px;
        letter-spacing: -0.4px;
    }
}

.subscribe__block{
    display: grid;
   
    input{
        padding: 27px 30px;
        border-radius: 65px;
        margin-top: 30px;
        margin-left: -21px;
    }
    input::placeholder{
        font-family: 'Jost';
        font-size: 22px;
        font-weight: 400;
    }
    &-btn{
        margin-top: 27px;
        margin-left: -21px;
        @include btnBlack;
        width: 173px;
        height: 70px;
        @include centerAll;
        border-radius: 126px;
        font-size: 19px;
    }
}

@media screen and (max-width:1280px) {
    .footer{
        margin-top: 130px;
        &__subtitle{
            font-size: 25px;
        }
        &__right{
            margin-right: 0;
        }
        &__title{
            font-size: 35px;
        }
        &__subtitle-text{
            font-size: 25px;
        }
        &__left{
            width: 520px;
        }
    }
    .contact{
        &__title{
            font-size: 35px;
        }
    }
    .contact__items{
        margin-left: -30px;
        img{
            width: 50px;
            margin-left: 18px;
        }
    }
    .contact__title-two{
        font-size: 35px;
    }
    .subscribe__block input {
        padding: 20px 30px;
        margin-left: -28px;
    }
    .subscribe__block-btn{
        height: 55px;
        font-size: 15px;
    }
}

@media screen and (max-width:1125px) {
    .footer{
        &__subtitle{
            font-size: 22px;
            line-height: 27px;
        }
        &__title{
            margin-top: 77px;
        }
        &__subtitle-text{
            font-size: 22px;
            line-height: 27px;
        }
        &__left{
            width: 430px;
        }
    }
}