@import 'config/vars';

.contact__item{
    margin-top: 25px;
    margin-left: -21px;
    display: flex;
    align-items: center;
    p{
        font-family: 'Jost';
        font-size: 30px;
        font-weight: 400;
        color: $gray;
        margin-left: 23px;
    }
}
@media screen and (max-width:1280px) {
    
    .contact{
        &__title{
            font-size: 35px;
        }
    }
    .contact__item {
        p{
            font-size: 20px;
        }
    }
}
@media screen and (max-width:1125px) {
    .contact__item {
        margin-top: 20px;
        p{
            font-size: 20px;
        }
        img{
            width: 50px;
        }
    }
    .contact__items {
        margin-left: -36px;
    }
}