@import 'config/mixins';

.customer__item{
    width: 468px;
    height: 648px;
    background: white;
    border-radius: 25px;
    &-descr{
        font-family: 'Jost';
        font-weight: 400;
        font-size: 20px;
        padding-left: 40px;
        padding-right: 40px;
    }
    &-descr{
        padding-left: 40px;
        padding-top: 30px;
        line-height: 30px;
    }
}
.nameBlock{
    padding-left: 39px;
    padding-right: 40px;
    padding-top: 40px;
   display: flex;
   align-items: center;}
.name{
    font-family: 'Jost';
    font-weight: 600;
    font-size: 24px;
    
}
.stars__items-item{
   
    margin-left: 20px;
}

.personImg{
    border-radius: 25px 25px 0px 0px; 
}
.swiper-slide:nth-child(1) .customer__item{
    margin-left: 25px;
}
.swiper-slide:nth-child(2) .customer__item{
    margin-left: 14px;
}
.swiper-slide:nth-child(3) .customer__item{
    margin-left: 3px;
}
.stars__items-item img:nth-child(2){
    margin-left: -6px;
}
.stars__items-item img:nth-child(3){
    margin-left: -4px;
}
.stars__items-item img:nth-child(4){
    margin-left: -6px;
}
@media screen  and (max-width:1180px){
    .customer__item{
        width: 430px;
    }
}