@import 'config/mixins';

.header{
    padding-top: 24px;
    @include justifyBeetwen; 
    &__cartBtn{
        width: 56px;
        height: 56px;
        @include centerAll;
        background: white;
        border-radius: 50%;
        margin-right: 20px;
    }
    &__buyBtn{
        font-family: "Jost";
        font-size: 15px;
        font-weight: 500;
        @include btnBlack;
        padding: 16px 30px;
        border-radius: 63px;
    }
    &__btns{
        @include centerAll;
    }
    &__row{
        display: flex;
        align-items: center;
    }
}
.close{
    display: none;
}
.burger{
    width: 20px;
    height: 20px;
    display: none;
}
.logo-burger{
    display: none;
}

@media screen and (max-width:1325px) {
    .header__row{
        flex-direction: row-reverse;
    }
    .close{
        display: block;
        position: absolute;
        left: -350px;
        top: 20px;
        z-index: 2;
        width: 20px;
        height: 20px;
        transition: all ease 1s;
    }
    .burger{
        display: block;
    }
    .logo-burger{
        display: block;
        position: absolute;
        left: -300px;
        transition: all ease 1s;
        z-index: 2;
    }
}